import React, { useState, useEffect } from "react"
import { Link, Trans, useI18next, useTranslation} from "gatsby-plugin-react-i18next"
import Logo from "../assets/svg/logo.svg"
import Search from "../assets/svg/search.svg"
import Language from "../assets/svg/language.svg"
import CloseSearch from "../assets/svg/times-solid.svg"

const Header = ({ title, menuLinks, headerIcons }) => {
  const {languages, originalPath} = useI18next();
  const [widthDimention, setWidthDimention] = useState(
    typeof window !== "undefined" ? window.innerWidth : null
  )

  const [showMenu, setShowMenu] = useState(false)
  const [classToggle, setClassToggle] = useState("menu-btn")
  const [classMenu, setClassMenu] = useState("menu-div")
  const [classMenuIcons, setClassMenuIcons] = useState("menu-div-icons")
  const [classOpacity, setClassOpacity] = useState("menu")
  const [showLang, setShowLang] = useState(false) 
  const [showSearch, setShowSearch] = useState(false)

  const toggleMenu = () => {
    if (!showMenu) {
      setClassToggle("menu-btn close")
      setClassMenu("menu-div show")
      setClassMenuIcons("menu-div-icons hide")
      setClassOpacity("menu opacity")
    } else {
      setClassToggle("menu-btn")
      setClassMenu("menu-div")
      setClassMenuIcons("menu-div-icons")
      setClassOpacity("menu")
    }
    setShowMenu(!showMenu)
  }

  const showLangOptions = () => {
    setShowLang(!showLang)
  }

  const showSearchOptions = () => {
    setShowSearch(!showSearch)
  }

  useEffect(() => {
    function handleResize() {
      setWidthDimention(
        typeof window !== "undefined" ? window.innerWidth : null
      )
    }

    if (typeof window !== "undefined") {
      window.addEventListener("resize", handleResize)
      return () => window.removeEventListener("resize", handleResize)
    }
  }, [])

  return (
    <header className="header">
      <div className="header-left">
        <LogoLink classToggle={classToggle} />
      </div>
      <ToggleMenu onClick={toggleMenu} classToggle={classToggle} />
      <div className="header-right">
          <NavHeader
            title = {title}
            menuLinks={menuLinks}
            headerIcons={headerIcons}
            showMenu={showMenu}
            classMenu={classMenu}
            classMenuIcons={classMenuIcons}
            classOpacity={classOpacity}
            widthDimention={widthDimention}
            languages = {languages}
            originalPath = {originalPath}
            showLang = {showLang}
            showLangOptions = {showLangOptions}
            showSearch={showSearch}
            showSearchOptions = {showSearchOptions}
          />
      </div>
    </header>
  )
}

const ToggleMenu = ({ onClick, classToggle }) => (
  <div className={classToggle} onClick={onClick}>
    <div className="btn-line"></div>
    <div className="btn-line"></div>
    <div className="btn-line"></div>
  </div>
)

const LogoLink = ({ classToggle }) => (
  <Link to="/" className="logo">
    {classToggle === "menu-btn" ? <Logo /> : null}
  </Link>
)

const NavHeader = ({ title, menuLinks, headerIcons,  showMenu, classMenu, classMenuIcons, classOpacity, widthDimention, languages, originalPath, showLang, showLangOptions, showSearch, showSearchOptions}) => {
  const {t} = useTranslation(['header'])
  return(
    <nav className={classOpacity}>
      {widthDimention > 600 || showMenu ? (
    <div className="menu-branding">
      <div className="portrait"></div>
    </div>):null}
    {widthDimention > 600 || showMenu ? (
      <div className={classMenu}>
        <ul className="menu-nav">
          {menuLinks.map(item => (
            (widthDimention > 600 && item.name === "BLOG") || (widthDimention < 600 && t(`header:${item.name}`,item.name) === t(`header:${title}`,title)) ? 
              null :
            <li className="nav-item" key={item.name}>
              <Link to={item.link}>
                <strong><Trans i18nKey={`header:${item.name}`}>{item.name}</Trans></strong>
              </Link>
            </li>
          ))}
        </ul>
      </div>
    ) : null}
    <div className={classMenuIcons}>
      <ul className="menu-nav">
        {headerIcons.map(item =>
              <li className="nav-item" key={item.name}>
                {verifyIcon(t, item.name, languages, originalPath, showLang, showLangOptions, showSearch, showSearchOptions)}
              </li>
        )}
      </ul>
    </div>    
  </nav>
  )
}
const verifyIcon = (t, iconName, languages, originalPath, showLang, showLangOptions, showSearch, showSearchOptions) => {
  switch (iconName) {
    case "Search":
      {/*return (
        <div className="search">
          {
            !showSearch ? (
              <div className="search-icon" onClick={showSearchOptions}>
                <Search />
              </div>
            ) : null
          }
          {
            showSearch ? (
              <div className="search-div">
                <form className="search-form">
                  <input 
                    className="search-input"
                    type="text"
                    name="search"
                    placeholder={t('header:search', 'Search')}
                  />
                  <button className="search-close" onClick={showSearchOptions}>
                    <CloseSearch/>
                  </button>
                </form>
              </div>
            ) : null
          }
        </div>
        )*/}
        return null
    case "Language":
      return(
        <div className="lang">
          <div className="lang-icon" onClick={showLangOptions}>
            <Language />
          </div>
          {
            showLang ? ( 
              <div className="lang-div"> 
                <div className="lang-list">
                {languages.map(lang => (
                  <Link className="lang-item" to={originalPath} language={lang} key={lang}>
                    {lang}
                  </Link>
                ))}
              </div>
              <div id="lang-div1"></div>
              <div id="lang-div2"></div>
            </div>
           ) : null
          }
         
        </div>
      )
    default:
      console.log(iconName)
  }
}

export default Header
